import React from 'react';

const Card = ({id,name,mail}) => {
	return(
		<div className='tc bg-light-green dib br3 pa3 ma2 grow bw2 shadow-5'>
		    <img alt="robots" src= {`https://robohash.org/${id}?100x100`}/>
		    <div>
		        <h2>{name}</h2>
		        <p>{mail}</p>
		    </div>
	    </div>
	);
}

export default Card;